html {
  font-family: sans-serif;
 }
 p {
  line-height: 1.5;
 }
 h1 {
  font-family: billabong, 'billabongregular';
  text-align: center;
  font-weight: 100;
  font-size: 10rem;
  margin: 2rem 0;
  letter-spacing: -1px;
  color: white;
  text-decoration: none;
  margin-block-end: 0px;
  margin-block-start: 0px;
 }

 .h1-border{
   background-color: rgb(235, 130, 147);
   width: 100%;
   height: 30%;
   border: 8px;
   border-color: white;
   border-style: dotted;
   margin-block-end: 0px;
   margin-block-start: 0px;
 }

.photoGrid {
  display: flex;
  flex-wrap: wrap;
  max-width: 1000px;
  margin: 0 auto;
 }

 .figure {
  width: 50%;
  border: 1px solid rgb(235, 130, 147);
  padding: 2rem;
  margin: 2rem 2rem 2rem 2rem;
 }
 .photo {
  max-width: 250px;
  max-height: 250px;
 }

 .footer{
   color: #d3d3d3;
   font-size: 1.3rem;
   padding-top: 2rem;
 }